.checkbox_element{width:10px;height:10px;padding:8px;background-color:hsla(0,0%,100%,.3333333333);border-radius:2px;position:relative;cursor:pointer;transition:all .2s ease 0s;z-index:0}.loginForm__form{display:flex;flex-direction:column;gap:20px}.loginForm__checkbox{opacity:0;position:absolute}.loginForm__checkbox:checked+.checkbox_element{background-color:#c163ff}.loginForm__checkbox:checked+.checkbox_element::before{content:"";background:url("../../../img/icons8-галочка.svg");background-repeat:no-repeat;background-position:center;background-size:contain;display:block;position:absolute;z-index:1;width:100%;height:100%;top:0;left:0}.loginForm__buttonContainer{display:flex;align-items:center;justify-content:center}.loginForm__label{display:flex;align-items:center;gap:20px;position:relative}.validatedForm__formfield{display:flex;align-items:flex-start;flex-direction:column;gap:15px;position:relative}.validatedForm__input{padding:10px 15px}.validatedForm__showIcon{height:40%;position:absolute;top:50%;right:1%;transform:translate(0, -23%);cursor:pointer;transition:transform .2s ease}.validatedForm__showIcon:hover{transform:translate(0, -23%) scale(1.02)}.validatedForm__showIcon:active{transform:translate(0, -23%) scale(0.95)}.error{font-size:16px;color:#ff6e0e}